export default function AnimatedCards() {
  const items = [
    { code: '01', title: 'Clarity under complexity', desc: 'Distilling focus and decision frameworks where ambiguity dominates.' },
    { code: '02', title: 'Human behaviour meets systems', desc: 'Designing for how people actually act inside technical and regulated environments.' },
    { code: '03', title: 'Auditability and trust', desc: 'Practices that leave clear trails for governance and accountability.' },
    { code: '04', title: 'Calm execution', desc: 'Deliberate, low-noise delivery that de-risks outcomes.' },
  ]

  return (
    <div className="mt-8 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 gap-4">
      {items.map(item => (
        <div
          key={item.title}
          className="system-card group"
          style={{ transition: 'transform 0.18s ease, box-shadow 0.18s ease' }}
          onMouseEnter={e => { (e.currentTarget as HTMLDivElement).style.transform = 'translateY(-4px)' }}
          onMouseLeave={e => { (e.currentTarget as HTMLDivElement).style.transform = '' }}
        >
          <span className="briefing-id">{item.code}</span>
          <h4 className="mt-3 text-[0.9375rem] font-medium text-[var(--color-text)] leading-snug">{item.title}</h4>
          <p className="mt-2 text-[0.8125rem] text-[var(--color-muted)] leading-relaxed">{item.desc}</p>
        </div>
      ))}
    </div>
  )
}
